@font-face {
  font-family: 'Montserrat';
  src: local('Montserrat'),
    local('Montserrat-Light'),
    url(../fonts/Montserrat-Light) format('woff2'),
    url(../fonts/Montserrat-Light.woff) format('woff'),
    url(../fonts/Montserrat-Light.eot) format('eot');
  font-style: normal;
  font-weight: 300;
  font-display: swap;
}

